import { Component, OnInit }  from '@angular/core';

import { Recipe }             from './recipe'
import { RecipeService }      from './recipe-service'
import { FirebaseRecipeService } from 'app/shared/firebaseRecipe.service'


@Component({
  selector: 'rb-recipes',
  template: require('./recipes.component.html')
})
export class RecipesComponent implements OnInit {
  recipes:Recipe[]=[];
  selectedRecipe:Recipe;
  constructor(private _recipeService:RecipeService,
              private srv:FirebaseRecipeService) { }

  ngOnInit() {
    // this.recipes=this._recipeService.getRecipes();
    console.log("Getting recipes from Firebase");
    this.loadRecipes();
    this.srv.onChanges.subscribe(res=>this.loadRecipes());
  }

  loadRecipes(){
    console.log("Loading updated recipes");
    this.srv.getRecipes()
      .subscribe(res=>{console.log(res);this.recipes=res;},
      error=>console.log(error));
  }
}



