import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'
import { Subscription } from 'rxjs/Rx'
import { FormBuilder, 
  FormArray, 
  FormGroup, 
  FormControl,
  Validators }  from '@angular/forms'

import { Recipe } from '../recipe'
import { RecipeService }  from '../recipe-service'
import { Ingredient }  from 'app/shared/ingredient'
import { FirebaseRecipeService } from 'app/shared/firebaseRecipe.service'

@Component({
  selector: 'rb-recipe-edit',
  template: require('./recipe-edit.component.html')
  
})
export class RecipeEditComponent implements OnInit, OnDestroy {
  recipeForm : FormGroup;
  private subscription: Subscription;
  private recipeId : string;
  selectedRecipe=new Recipe();
  title:String;
  isNew=true;
  isLoaded=false;

  constructor(private route:ActivatedRoute,
              private rsrv:RecipeService,
              private fb:FormBuilder,
              private router:Router,
              private srv:FirebaseRecipeService) { }

  ngOnInit() {
    this.selectedRecipe.ingredients=[];
    this.isNew=true;
   
    if(this.route.snapshot.params.hasOwnProperty('id')){
      this.isNew=false;
      this.recipeId = this.route.snapshot.params['id'];
      this.loadRecipe();
    } 
    this.initForm();
  }

  loadRecipe(){
    console.log("Get Recipe");
    this.srv.getRecipe(this.recipeId)
      .subscribe(res=>{ this.selectedRecipe=res;
                        this.initForm();
                      },
                  error=>console.log(error));
  }

  ngOnDestroy(){
    // this.subscription.unsubscribe;
  }

  onSubmit(){
    const newRecipe:Recipe = this.recipeForm.value;
    if(this.isNew){
      console.log("New Recipe");
      
      this.srv.postRecipe(newRecipe)
        .subscribe(res=>console.log(res),
                  error=>console.log(error),
                  ()=>this.srv.onChanges.emit(true));

    }else{
      console.log("Update Recipe");
      newRecipe.key= this.recipeId;
      this.srv.updateRecipe(newRecipe)
        .subscribe(res=>console.log(res),
                  error=>console.log(error),
                  ()=>this.srv.onChanges.emit(true));;
    }
    this.navigateBack();
  }

  onCancel(){
    this.navigateBack();
  }

  onRemoveIngredient(i){
    (<FormArray>this.recipeForm.controls['ingredients']).removeAt(i);
  }

  onAddIngredient(name,amount){
    (<FormArray>this.recipeForm.controls['ingredients']).push(
      new FormGroup({
            name:new FormControl(name,Validators.required),
            amount:new FormControl(amount,
                                  [ Validators.required,
                                    Validators.pattern("\\d+")
                                  ])
          })
    );
  }
  private navigateBack(){
    this.router.navigate(["/recipes"]);
  }

  private initForm(){
    console.log("initForm");
    console.log(this.selectedRecipe);
    let recipeName = "";
    let recipeDescription = "";
    let recipeImagePath = "";
    let recipeIngredients:FormArray = new FormArray([]);
    this.title = (this.isNew)?"New ":"Edit";
    
    if(!this.isNew){
      recipeName = this.selectedRecipe.name;
      recipeImagePath = this.selectedRecipe.imagePath;
      recipeDescription = this.selectedRecipe.description;
      
      //add ingredients
      if (this.selectedRecipe.ingredients!=null)
      {
        this.selectedRecipe.ingredients.forEach(element => {
          recipeIngredients.push(
            new FormGroup({
              name:new FormControl(element.name,Validators.required),
              amount:new FormControl(element.amount,
                                    [ Validators.required,
                                      Validators.pattern("\\d+")
                                    ])
            })
          );        
        });
      }
    }

    //build the forms
    this.recipeForm = this.fb.group({
      name:[recipeName,Validators.required],
      imagePath:[recipeImagePath,Validators.required],
      description:[recipeDescription,Validators.required],
      ingredients:recipeIngredients
    })
  }

}



