import { Component, OnInit, Input, OnDestroy }  from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'
import { Subscription } from 'rxjs/Rx'

import { Recipe }             from '../recipe'
import { ShoppingListService }  from 'app/shopping-list/shopping-list.service'
import { AuthService } from "app/shared/auth.service";
import { FirebaseRecipeService } from 'app/shared/firebaseRecipe.service'


@Component({
  selector: 'rb-recipe-details',
  template: require('./recipe-details.component.html')
})
export class RecipeDetailsComponent implements OnInit, OnDestroy {
  selectedRecipe: Recipe;
  recipeId:string; 
  isAuthenticated = false;

  private subscription: Subscription;
  constructor(private sls: ShoppingListService,
              private rtr: Router,
              private router: ActivatedRoute,
              private authService: AuthService,
              private srv:FirebaseRecipeService) {
    this.authService.isAuthenticated().subscribe(
      authStatus => this.isAuthenticated = authStatus);
    }

  ngOnInit() {
    this.subscription = this.router.params
                          .subscribe(params=>{
                            this.recipeId=params['id'];
                            this.getRecipe();
                            });
  }
  getRecipe(){
    this.srv.getRecipe(this.recipeId)
      .subscribe(res=>this.selectedRecipe=res,
                  error=>console.log(error));
  }

  ngOnDestroy(){
    this.subscription.unsubscribe;
  }

  isAuth() {
      return this.isAuthenticated;
  }

  onAddToShoppingList(){
    this.sls.addItems(this.selectedRecipe.ingredients);
  }

  onEdit(){
    this.rtr.navigate(["/recipes",this.recipeId,"edit"]);
  }
  onDelete(){
    console.log("Key"+this.recipeId);
    this.srv.deleteRecipe(this.recipeId)
      .subscribe(()=>this.srv.onChanges.emit(true));
    this.rtr.navigate(["/recipes"]);
  }
}



